*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}
body{
  overflow-x: hidden
}

@import url('https://fonts.googleapis.com/css2?family=Montserrat&family=Nunito+Sans&family=Playfair+Display&family=Poppins&family=Roboto&family=Roboto+Slab&display=swap');


.header {
  position: sticky;
  background-color: white;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  height: 70px;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 99;
  transition: 0.3s ease-in;
  overflow: hidden;
  background-color: transparent;
}

.btn-form-contact{
  width: 160px;
    height: 40px;
    background: transparent;
    color: #ffffff;
    border: 1.5px solid #a2a2a2;
    margin-top: 30px;
    border-radius: 5px;
    font-weight: 600;
    font-size: 14px;
}
.btn-form-contact:hover{
  background-color: #0e66f9;
  border: none;
  transition: 0.5s;
  color: #ffffff;
}

.activeH {
  background-color: white;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}

.header .navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  /* max-width: 1240px; */
  margin: auto;
  height: 100%;
  padding: 0 40px;
}

.header .nav-menu a {
  font-size: 18px;
  color: black;
  text-decoration: none;
  position: relative;
  top: 6px;
  padding: 0 5px;
}


.header .navbar img {
  display: block;
  max-width: 100%;
  width: 150px;
  height: auto;
  object-fit: cover;
  margin: auto;
}

.header .nav-menu {
  display: flex;
}

.header .nav-item {
  padding: 1rem;
  font-weight: 500;
}

.header .nav-item a:hover {
  padding-bottom: 12px;
  border-bottom: 3px solid black;
}

.hamburger {
  display: none;
}

.hamburger i {
  color: #C00100;
  font-size: 25px;
  font-weight: bold;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

.btn-primary {
  background-color: #C10100;
  font-size: 17px;
  display: block;
  margin: auto;
  height: 40px;
  border: none;
  width: 195px;
  max-width: 100%;
  border-radius: 2px;
  color: white;
  font-family: 'Gilroy-BoldItalic';
  cursor: pointer;
  
}

/* .btn-primary:hover{
  background: #0677c7;
  transition: all 0.5s;
} */
.btn-primary:hover i{
  transform: scale(1.3) translate(3px);
  transition: all 0.4s;
}

@media screen and (max-width: 940px) {
  .header {
    max-width: 100%;
    background-color: white;
  }

  .header .navbar {
    max-width: 100%;
  }

  .hamburger {
    display: block;
  }

  .nav-menu {
    position: fixed;
    left: -100%;
    top: 90px;
    flex-direction: column;
    background-color: white;
    width: 100%;
    height: 100vh;
    z-index: 999;
    text-align: center;
    transition: 0.3s;
  }

  .nav-menu.active {
    left: 0;
  }

  .header .navbar img {
    width: 150px;
  }

  .btn-primary{
    display: block;
    margin: auto;
  }
}

