/* content */
.content {
  text-align: center;
}

.content h1 {
  letter-spacing: 1px;
}


/* form */
.form {
  width: 100%;
  margin: 80px 0;
  padding: 0px 40px;
}


.css-2royc9{
  
}

.top-form,
.bottom-form {
  width: 100%;
  min-height: 65px;
  margin: 30px 0;
}

.form input[type="text"],
.form textarea, .form input[type="email"]  {
  border: none;
  background-color: #eeeeee;
  padding: 20px 10px;
  outline: none;
  border-radius: 5px;
  width: 100%;
  transition: all 0.2s ease;
  font-size: 14.5px;
  font-weight: 500;
  color: #000000;
  opacity: 0.8;
}

.form .fileup{
  border: none;
  background-color: #eeeeee;
  padding: 16px 10px;
  outline: none;
  border-radius: 5px;
  width: 100%;
  transition: all 0.2s ease;
  font-size: 16px;
  font-weight: 500;
  color: transparent;
}

.form input:focus,
.form textarea:focus {
  outline: none;
}

.form .label {
  margin-bottom: 5px;
  border: none;
  background-color: #eeeeee;
  padding: 16px 10px;
  outline: none;
  border-radius: 5px;
  width: 100%;
  transition: all 0.2s ease;
  font-size: 16px;
  font-weight: 500;
  text-transform: capitalize;
}

